<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/articleCategory"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse='true'>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="updateRules" :model="formData" label-width="210px">

        <el-form-item label="分类名称" prop="name"  :error='errorMessage.name' >
          <el-input v-model="formData.name"  :autofocus="true" ref="first"></el-input>
        </el-form-item>
        <el-form-item label="排序值" prop="sortNum" :error='errorMessage.sortNum'>
          <el-input maxlength="8" v-model.number="formData.sortNum"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description" :error='errorMessage.description'>
          <el-input v-model="formData.description"></el-input>
        </el-form-item>
         <el-form-item label="是否显示" prop="show" >
          <cms-switch v-model="formData.show"></cms-switch>
        </el-form-item>
        <el-form-item label="是否显示在底部导航" prop="bottomShow" >
          <cms-switch v-model="formData.bottomShow"></cms-switch>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
          <el-button @click="update" type="primary" size="small">保存修改</el-button>
        <el-button  @click="reset"  size="small">重置</el-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  artCategoryGet,
  artCategoryEdit,
  artCategoryTree
} from '@/api/article/articleCategory.js'

var defaultData = {
  'name': '',
  'show': true,
  'sortNum': '',
  'bottomShow': false,
  'id': '',
  'parentId': 0,
  'parentIds': [0],
  'description': ''
}
export default {
  mixins: [form],
  data() {
    var validateSortnum = (rule, value, callback) => {
      if (value >= 1 && value <= 100) {
        callback()
      } else {
        callback(new Error('排序值为1到100的整数'))
      }
    }
    return {
      options: [],
      props: {
        value: 'id',
        label: 'name',
        children: 'childs',
        disabled: 'isAuth'
      },
      formData: Object.assign({}, defaultData),
      updateRules: {// 修改规则
        name: [rules.required('标题必填'), rules.len(1, 10)],
        sortNum: [rules.required('排序值必填'), rules.number(), rules.custom(validateSortnum)],
        description: [rules.len(1, 20)]
      }

    }
  },
  methods: {
    getFormData(id) { // 获取分类信息
      artCategoryGet({ id: id }).then(res => {
        this.formData = res.data
        if (res.data.nodeIds.length <= 1) {
          this.formData.parentIds = [0]
        } else {
          this.formData.parentIds = res.data.nodeIds
        }
      })
    },
    getMenuTree() { // 获取菜单节点
      artCategoryTree().then(
        res => {
          const root = [{ id: 0, name: '根目录' }]
          this.options = root.concat(res.data)
        }
      )
    },
    update() {
      const params = this.$cms.deepCopy(this.formData)
      params.parentId = params.parentIds[params.parentIds.length - 1]
      this.updateForm(artCategoryEdit, params, '/articleCategory/list', 'list')
    },
    reset() {
      this.getFormData(this.$route.query.id)
      this.$refs.form.resetFields()
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
    this.getMenuTree()
  }
}
</script>




